import { useSearchParams } from 'react-router-dom';
import { motion } from "framer-motion";
import useMediaQuery from '@mui/material/useMediaQuery';

import { Welcome } from '.././pages/Welcome';
import { Login } from '.././pages/Login';

import Box from '@mui/joy/Box';
import Sheet from '@mui/joy/Sheet';
import Typography from '@mui/joy/Typography';
import Button from '@mui/joy/Button';
import Stack from '@mui/joy/Stack';

import { useColorScheme } from '@mui/joy/styles';
import { useEffect } from 'react';
import { Dashboard } from '../pages/Dashboard';

function Error({ go }) {
    return (
        <Sheet sx={{
            display: "flex",
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            alignContent: 'center',
            minHeight: '100vh',
            overflow: 'hidden',
            fontFamily: 'Inter',
            padding: '0% 5% '
        }}>
            <Stack spacing={4} sx={{
                display: 'flex',
                alignItems: 'center'
            }}>
                <Sheet sx={{
                    display: 'flex',
                    flexDirection: 'row',
                    flexWrap: 'nowrap',
                    justifyContent: 'center'
                }}>
                    <img src={process.env.PUBLIC_URL + '/assets/four.svg'} alt='4' width="30%"></img>
                    <img src={process.env.PUBLIC_URL + '/assets/zero.svg'} alt='0' width="30%"></img>
                    <img src={process.env.PUBLIC_URL + '/assets/four.svg'} alt='4' width="30%"></img>
                </Sheet>
                <Typography level='h2'>    </Typography>
                <Button onClick={() => { go(""); }}>   </Button>
            </Stack>
        </Sheet>
    );
}



export function Layout() {
    const matches = useMediaQuery('(min-width:800px)');
    const { mode, setMode } = useColorScheme();
    const [searchParams, setSearchParams] = useSearchParams();
    const searchPage = searchParams.get('p') || '';
    var showPage = <Welcome />

    function Animation() {
        useEffect(() => {
            setMode('system');
        }, []);
        return (
            <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{
                    duration: 0.2,
                    delay: 0.2,
                }}>

                {showPage}
            </motion.div>
        );
    }

    function go(to = '') {
        setSearchParams({ p: to });
    }


    if (searchPage === '') {
        showPage = <Welcome searchParams={searchParams} setSearchParams={setSearchParams} go={go} appearance={mode} matches={matches} />
    } else if (searchPage === 'login') {
        showPage = <Login searchParams={searchParams} setSearchParams={setSearchParams} go={go} />
    } else if (searchPage === 'dashboard') {
        showPage = <Dashboard searchParams={searchParams} setSearchParams={setSearchParams} go={go} matches={matches}   />
    } else {
        showPage = <Error go={go} />
    }

    return (
        <Box sx={{
            padding: '0px',
            margin: '0px',
        }}>
            <main>
                {/* {showPage} */}
                <Animation />
            </main>
        </Box>
    );

}